import React, { Component } from 'react'

export default class CodingSection extends Component {
	render() {
		return (
			<div className="site-section section-2" id="coding">
				<div className="container">
				  <div className="row">
					<div className="col-lg-6 mb-5">
					  <span className="section-sub-title d-block">Things Worth Executing</span>
					  <h2 className="section-title">Coding</h2>
					  <p>Here you can find my resume, links to my personal data science projects, and descriptions of key projects from my education and work history.</p>
					</div>
				  </div>
				</div>
				<div className="owl-carousel nonloop-block-13">
				  <a className="work-thumb" href="files/softengresume.pdf" target="_blank">
					<div className="work-text">
					  <h3>Resume</h3>
					  <span className="category">Click to view</span>
					</div>
					<img src="images/updated_resume_capture.png" alt="Resume" className="img-fluid" />
				  </a>
				  <a className="work-thumb" href="https://www.mouser.com/technical-resources/" target="_blank" rel="noreferrer">
					<div className="work-text">
					  <h3>Search from Scratch</h3>
					  <span className="category">Click to play with the search functionality I built from<br/>the ground up - web crawler, search engine, and all.</span>
					</div>
					<img src="images/trc.PNG" alt="Screen capture of Mouser Electronic's Technical Resource Center" className="img-fluid" />
				  </a>
				  <a className="work-thumb" href="https://public.tableau.com/views/TheBibleAsData/TheBibleAsData?:language=en-US&:display_count=n&:origin=viz_share_link" target="_blank" rel="noreferrer">
					<div className="work-text">
					  <h3>The Bible as Data</h3>
					  <span className="category">Click here to view Tableau visualization</span>
					</div>
					<img src="images/bible_data_resize.jpg" alt="The Bible" className="img-fluid" />
				  </a>
				  <a className="work-thumb" href="https://www.mouser.com/technical-resources/conversion-calculators/resistor-color-code-calculator" target="_blank" rel="noreferrer">
					<div className="work-text">
					  <h3>Resistor Color Code Calculator built with Knockout.js</h3>
					  <span className="category">Click to play with the dynamic and responsive resistor color code calculator I built using knockout.js - just one of the engineer-specific calculators I built for my company.</span>
					</div>
					<img src="images/resistor_color_code_calc_screencap.png" alt="Screen capture of Mouser Electronic's Resistor Color Code calculator" className="img-fluid" />
				  </a>
				</div>
			  </div>
		)
	}
}